@import url("https://fonts.googleapis.com/css?family=Anton|Work+Sans:400|Heebo:900");
.flex-both-align {
  display: flex;
  align-items: center;
  justify-content: center; }

.flex-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: center;
  align-items: flex-start; }

.vertical-align, .main-section .copy, .main-section .circle, .video-wrapper, .carousel .titles, .arrow, body.projects section.main-section .details ul li:before {
  position: relative;
  top: 50%;
  transform: translateY(-50%); }

.horizontal-align, .scroll-section.contact .me {
  position: relative;
  left: 50%;
  transform: translateX(-50%); }

.align-both, nav ul li:before, .main-section.projects .carousel .about, body.about .intro .circle video, body.projects section.main-section .img-intro {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

* {
  box-sizing: border-box;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility; }

html {
  font-size: 62.5%; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: 300 16px "Work Sans", sans-serif; }

p {
  color: #fff; }

h1,
h2,
h3,
h4,
h5 {
  font-family: "Heebo", sans-serif;
  letter-spacing: 0; }

a {
  text-decoration: none; }

.link {
  position: relative;
  color: #fff;
  margin-top: 5px;
  display: inline-block;
  padding: 5px;
  z-index: 1;
  outline: none;
  transition: .1s ease-in; }
  .link:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: #374A67;
    transition: .1s height ease-in;
    z-index: -1; }
  .link:hover:before {
    height: 100%; }

.barba-container {
  width: 100%;
  overflow: hidden; }

