@keyframes scaleUpIn {
  0% {
    opacity: 0;
    transform: scale(0.8) translate3d(0, 10%, 0); }
  100% {
    transform: scale(1) translate3d(0, 0, 0);
    opacity: 1; } }

@keyframes scaleDownOut {
  0% {
    transform: scale(1) translate3d(0, 0, 0);
    opacity: 1; }
  100% {
    transform: scale(0.8) translate3d(0, -10%, 0);
    opacity: 0; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes fadeInLeft {
  0% {
    transform: translate(-20vw, -50%);
    opacity: 0; }
  100% {
    transform: translate(0, -50%);
    opacity: 1; } }

@keyframes fadeInRight {
  0% {
    transform: translate(20vw, -50%);
    opacity: 0; }
  100% {
    transform: translate(0, -50%);
    opacity: 1; } }

@keyframes fadeUpIn {
  0% {
    transform: translateY(5vw);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes circleIn {
  0% {
    width: 120vmax;
    height: 120vmax;
    background-color: #374A67; }
  100% {
    width: 45vw;
    height: 45vw; } }

@keyframes circleOut {
  0% {
    width: 45vw;
    height: 45vw; }
  100% {
    width: 120vmax;
    height: 120vmax;
    background-color: #374A67; } }

@keyframes circleInMobile {
  0% {
    width: 120vmax;
    height: 120vmax;
    background-color: #374A67; }
  100% {
    width: 60vmax;
    height: 60vmax; } }

@keyframes circleOutMobile {
  0% {
    width: 60vmax;
    height: 60vmax; }
  100% {
    width: 120vmax;
    height: 120vmax;
    background-color: #374A67; } }

nav {
  position: fixed;
  top: 0;
  left: 0;
  padding: 20px;
  z-index: 1; }
  nav ul {
    list-style: none; }
    @media (max-width: 767px) {
      nav ul {
        padding-left: 5px; } }
    nav ul li {
      position: relative;
      display: inline-block;
      margin: 0 10px;
      line-height: 1; }
      @media (max-width: 767px) {
        nav ul li {
          margin: 0 2.5px;
          font-size: 14px; } }
      nav ul li:before {
        content: '';
        position: absolute;
        width: 30px;
        height: 30px;
        border-radius: 100%;
        background-color: #64718C;
        z-index: -1;
        opacity: 0;
        transition: opacity .2s ease-in; }
      nav ul li a {
        color: #fff; }

body.home nav ul li:first-of-type:before {
  opacity: 1; }

body.work nav ul li:nth-of-type(2):before {
  opacity: 1; }

body.about nav ul li:nth-of-type(3):before {
  opacity: 1; }

body.contact nav ul li:nth-of-type(4):before {
  opacity: 1; }

.main-section {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background-color: #374A67;
  transition: all ease-in-out 0.75s; }
  @media (min-width: 768px) {
    .main-section {
      border: 20px solid #fff; } }
  .main-section .copy {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    left: 12.5vw;
    height: 50vw; }
    @media (max-width: 767px) {
      .main-section .copy {
        left: 20px; } }
    .main-section .copy h1,
    .main-section .copy h2 {
      display: inline-table;
      position: relative;
      margin: 0;
      opacity: 0;
      color: #fff;
      font-family: "Heebo", sans-serif;
      line-height: 1;
      animation: fadeIn 0.375s ease-in-out forwards 0.375s; }
      @media (min-width: 768px) {
        .main-section .copy h1,
        .main-section .copy h2 {
          text-shadow: -8px 8px 25px rgba(55, 74, 103, 0.5); } }
    .main-section .copy h1 {
      font-size: 13vw; }
    .main-section .copy h2 {
      font-size: 5.1vw; }
    .main-section .copy a {
      margin-top: 30px;
      display: block; }
  .main-section .copy,
  .main-section .titles {
    transform: translate(0, -50%); }
  .main-section.active .copy,
  .main-section.active .titles {
    opacity: 0; }

.barba-container .main-section.active .circle .me {
  opacity: 0;
  animation: fadeIn .2s ease-in forwards .7s; }

.barba-container .main-section.active .copy,
.barba-container .main-section.active .titles {
  animation: fadeInRight 0.2s ease-in-out forwards 0.3s; }

.barba-container.landing-animation .main-section.active .circle .me {
  opacity: 0;
  animation: fadeIn .2s ease-in forwards 1.7s; }

.barba-container.landing-animation .main-section.active .copy,
.barba-container.landing-animation .main-section.active .titles {
  animation: fadeInRight 0.2s ease-in-out forwards 1.3s; }

.transition-out .main-section.active .circle .me {
  animation: fadeOut .2s ease-in forwards !important; }

.transition-out .main-section.active .copy,
.transition-out .main-section.active .titles,
.transition-out .main-section.active .btn {
  animation: fadeOut 0.375s ease-in-out forwards !important; }

